.navbar {
  background: $primaryColor;
  height: 10vh;
  color: $primaryTextColor;
  width: 100vw;
  .container {
    height: 100%; }
  .brand-name {
    margin-right: auto; }
  .nav-links {
    border-radius: 5px;
    cursor: pointer;
    padding: 0 15px;
    transition: all 500ms ease-in-out;
    &:hover {
      background: $primaryColorDark; }
    a {
      color: $primaryTextColor;
      text-decoration: none;
      letter-spacing: 1.5px;
      line-height: 2em;
      font-size: 1.2em; } } }
